module.exports = "import React from 'react';\n\nimport { DragConstraint, Layer, Frame, Drag } from 'buttery';\n\nexport default class Example extends React.Component {\n  state = {\n    x: 100,\n    y: 100,\n  };\n\n  _onDragEnd = ({ x, y }) => {\n    this.setState({ x, y });\n  };\n\n  render() {\n    return (\n      <Layer\n        frame={Frame(60, 60, 280, 280)}\n        style={{\n          backgroundColor: '#fcfefe',\n        }}\n      >\n        <Layer\n          frame={Frame(\n            Drag(this.state.x, {\n              min: 0,\n              max: 280 - 80,\n              bounce: true,\n              momentum: true,\n            }),\n            Drag(this.state.y, {\n              min: 0,\n              max: 280 - 80,\n              bounce: true,\n              momentum: true,\n            }),\n            80,\n            80\n          )}\n          onDragEnd={this._onDragEnd}\n          style={{\n            backgroundColor: '#49c6ae',\n          }}\n        />\n      </Layer>\n    );\n  }\n}\n"


