import React from 'react';

import { Layer } from 'buttery';

export default class Example extends React.Component {
  render() {
    return (
      <div>
        <Layer
          frame={{
            x: 160,
            y: 160,
            width: 80,
            height: 80,
          }}
          style={{
            backgroundColor: '#49c6ae',
          }}
        />
      </div>
    );
  }
}



